#main-sticky {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  padding: 10px;
  background: rgba(0, 0, 0, .5);
  @media screen and (max-width: 768px) {
    background-color: #008ec3;
    border-top: 1px solid white;
    padding: 0;
    .container {
      padding: 0;
    }
  }
  ul {
    align-items: center;
    li {
      flex: 1 1 33.3333%;
      @media screen and (max-width: 768px) {
        flex: 1 1 25%;
        border-right: 1px solid white;
        &:last-child {
          border-right: none;
        }
      }
      a {
        color: white;
        display: flex;
        align-items: center;
        justify-content: center;
        align-content: center;
        padding: 20px 15px;
        font-weight: bold;
        @include font-px-to-rem(16);
        @media screen and (max-width: 768px) {
          flex-direction: column;
          @include font-px-to-rem(14);
        }
        .fa {
          @media screen and (min-width: 769px) {
            margin-right: 5px;
          }
        }
      }
      &.tel {
        background-color: #eb8845;
      }
      &.mail {
        background-color: #008ec3;
      }
      &.download {
        background-color: black;
      }
    }
  }
}