#main-footer {
  .footer-top {
    h3 {
      line-height: 1.25;
      background-color: #3cc;
      padding: 30px 15px;
      color: #fff;
      text-align: center;
      position: relative;
      @include font-px-to-rem(27);
      @media screen and (max-width: 768px) {
        padding: 15px;
        @include font-px-to-rem(20);
      }
      &:after {
        display: block;
        content: '';
        width: 0;
        height: 0;
        border: 25px solid transparent;
        border-top-color: #3cc;
        position: absolute;
        top: 100%;
        @include h-align;
      }
    }
    .container {
      display: flex;
      justify-content: center;
      text-align: center;
      align-items: center;
      padding: 75px 15px;
      @media screen and (max-width: 768px) {
        flex-direction: column;
        padding: 30px 15px;
      }
      .button {
        background-color: #3cc;
        font-weight: bold;
        @include font-px-to-rem(16);
      }
    }
  }
  .footer-bottom {
    padding: 30px 0;
    border-top: 1px solid #ddd;
    nav {
      display: flex;
      justify-content: space-between;
      align-items: center;
      @media screen and (max-width: 768px) {
        justify-content: center;
      }
      ul {
        height: 100%;
        display: flex;
        @media screen and (max-width: 768px) {
          display: none;
        }
        li {
          margin-right: 5px;
          a {
            display: block;
            font-weight: 500;
            color: #333333;
            padding: 2px 5px;
            border-radius: 3px;
            @include font-px-to-rem(14);
            @include smooth-transition;
            &:hover {
              background: #008ec3;
              color: #fff;
              @include smooth-transition;
            }
          }
        }
      }
    }
  }
  .copyright {
    text-align: center;
    color: white;
    padding: 15px;
    background-color: #008ec3;
    @include font-px-to-rem(12);
  }
}