#main-header {
  padding: 0 20px;
  position: fixed;
  background-color: #fff;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0 0 5px rgba(0, 0, 0, .2);
  z-index: 999;
  @media screen and (max-width: 768px) {
    padding: 10px 0 10px 10px;
  }
  .navbar-brand {
    padding-right: 20px;
    img {
      height: 32px;
    }
  }
  .header-right {
    display: flex;
    nav {
      @media screen and (min-width: 769px) {
        display: block !important;
      }
      @media screen and (max-width: 768px) {
        display: none;
        position: fixed;
        top: 52px;
        left: 0;
        width: 100%;
        background-color: #fff;
        box-shadow: 0 5px 10px rgba(0, 0, 0, .2);
      }
      .navbar-nav {
        display: flex;
        align-items: center;
        height: 100%;
        @media screen and (max-width: 768px) {
          flex-wrap: wrap;
          border-top: 1px solid #ddd;
        }
        .nav-item {
          border-left: 1px solid #ddd;
          padding: 20px;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          text-align: center;
          @include smooth-transition;
          @media screen and (max-width: 980px) {
            padding: 10px;
          }
          @media screen and (max-width: 768px) {
            width: 100%;
            border-left: none;
            text-align: left;
            justify-content: flex-start;
            flex-wrap: wrap;
            border-bottom: 1px solid #ddd;
          }
          &:before {
            display: block;
            content: '';
            background-color: transparent;
            color: transparent;
            width: 0;
            height: 0;
            opacity: 0;
          }
          &.active, &:hover {
            background: #f6f6f6;
            cursor: pointer;
            @include smooth-transition;
            .nav-link {
              color: #018ec4;
            }
          }
          .nav-link {
            color: #333;
            text-align: center;
            font-weight: bold;
            @include font-px-to-rem(14);
            @media screen and (max-width: 768px) {
              display: block;
              width: 100%;
              text-align: left;
            }
          }
          &.dropdown {
            position: relative;
            .dropdown-menu {
              opacity: 0;
              visibility: hidden;
              position: absolute;
              top: 100%;
              left: 0;
              background-color: #fff;
              @include smooth-transition;
              @media screen and (min-width: 769px) {
                display: block !important;
              }
              @media screen and (max-width: 768px) {
                position: relative;
                opacity: 1;
                visibility: visible;
                display: none;
                background-color: transparent;
                margin-top: 10px;
              }
              .dropdown-item {
                display: block;
                color: #333333;
                text-align: left;
                padding: 5px 10px;
                @include font-px-to-rem(14);
                @include smooth-transition;
                &:hover {
                  background-color: #f6f6f6;
                  color: #018ec4;
                  @include smooth-transition;
                }
              }
            }
            &:hover {
              .dropdown-menu {
                opacity: 1;
                visibility: visible;
                @include smooth-transition;
              }
            }
          }
        }
      }
    }
    .header-information {
      font-weight: bold;
      padding: 10px 0 10px 20px;
      border-left: 1px solid #ddd;
      @media screen and (max-width: 768px) {
        display: none;
      }
      h2 {
        background: #000;
        color: #fff;
        border-radius: 2px;
        @include font-px-to-rem(12);
        text-align: center;
        padding: 2px;
      }
      a {
        display: flex;
        text-align: center;
        padding: 3px 0 0;
        &[href^='tel'] {
          color: #018ec4;
          @include font-px-to-rem(12);
        }
        &.header-mail {
          color: #333333;
          @include font-px-to-rem(12);
          align-items: center;
          .fa {
            margin-right: 5px;
          }
        }
      }
    }

  }
  #menu-sp-btn {
    display: none;
    position: fixed;
    z-index: 9999;
    top: 15px;
    right: 15px;
    width: 30px;
    height: 30px;
    @media screen and (max-width: 768px) {
      display: block;
    }
    span {
      display: block;
      width: 100%;
      height: 2px;
      background-color: #018ec4;
      margin-bottom: 6px;
      transition: transform .3s cubic-bezier(.175, .885, .32, 1.275) .1s, opacity .1s ease-in-out .1s, -webkit-transform .3s cubic-bezier(.175, .885, .32, 1.275) .1s;
      transform: translateZ(0);
      &:last-of-type {
        margin-bottom: 0;
      }
    }
    &.is-clicked {
      background-color: #fff;
      span {
        transition: transform .3s cubic-bezier(.175, .885, .32, 1.275) .1s, opacity .1s ease-in-out .1s, -webkit-transform .3s cubic-bezier(.175, .885, .32, 1.275) .1s;
        &:first-child {
          transform: translateY(9px) rotateZ(-135deg);
        }
        &:nth-child(2) {
          opacity: 0;
        }
        &:last-child {
          transform: translateY(-7px) rotateZ(135deg);
        }
      }
    }
  }
}