/* ====================================================
Base style & container
==================================================== */
body {
  color: black;
  font-size: 100%;
  line-height: 1.4;
  font-family: "Noto Sans CJK JP", " ProN", "Hiragino Kaku Gothic ProN", "", "", YuGothicMedium, YuGothic, "Yu Gothic", "", Meiryo, " ", "MS Gothic", "HiraKakuProN-W3", "TakaoEx", TakaoExGothic, "MotoyaLCedar", "Droid Sans Japanese", sans-serif;
  -webkit-font-smoothing: antialiased;
}

.container {
  display: block;
  margin: 0 auto;
  padding: 0 15px;
}

.container.container-lg {
  max-width: 1530px;
}

.pc {
  display: block;
}

.pc-in {
  display: inline-block;
}

.pc-flex {
  display: flex;
}

.pc-flex-in {
  display: inline-flex;
}

@media screen and (max-width: 768px) {
  .pc {
    display: none;
  }
  .pc-in, .pc-flex, .pc-flex-in {
    display: none;
  }
}

.sp {
  display: block;
}

.sp-in {
  display: inline-block;
}

.sp-flex {
  display: flex;
}

.sp-flex-in {
  display: inline-flex;
}

@media screen and (min-width: 769px) {
  .sp {
    display: none;
  }
  .sp-in, .sp-flex, .sp-flex-in {
    display: none;
  }
}

/* ====================================================
Float & clear
==================================================== */
.clearfix {
  *zoom: 1;
}

.clearfix:before {
  display: table;
  content: '';
}

.clearfix:after {
  display: table;
  content: '';
  clear: both;
}

