#main-header {
  padding: 0 20px;
  position: fixed;
  background-color: #fff;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  z-index: 999;
}

@media screen and (max-width: 768px) {
  #main-header {
    padding: 10px 0 10px 10px;
  }
}

#main-header .navbar-brand {
  padding-right: 20px;
}

#main-header .navbar-brand img {
  height: 32px;
}

#main-header .header-right {
  display: flex;
}

@media screen and (min-width: 769px) {
  #main-header .header-right nav {
    display: block !important;
  }
}

@media screen and (max-width: 768px) {
  #main-header .header-right nav {
    display: none;
    position: fixed;
    top: 52px;
    left: 0;
    width: 100%;
    background-color: #fff;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
  }
}

#main-header .header-right nav .navbar-nav {
  display: flex;
  align-items: center;
  height: 100%;
}

@media screen and (max-width: 768px) {
  #main-header .header-right nav .navbar-nav {
    flex-wrap: wrap;
    border-top: 1px solid #ddd;
  }
}

#main-header .header-right nav .navbar-nav .nav-item {
  border-left: 1px solid #ddd;
  padding: 20px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  -webkit-transition: all .3s ease;
  -moz-transition: all .3s ease;
  -ms-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
  -webkit-backface-visibility: hidden;
  -webkit-perspective: 1000px;
  -moz-perspective: 1000px;
  -ms-perspective: 1000px;
  perspective: 1000px;
}

@media screen and (max-width: 980px) {
  #main-header .header-right nav .navbar-nav .nav-item {
    padding: 10px;
  }
}

@media screen and (max-width: 768px) {
  #main-header .header-right nav .navbar-nav .nav-item {
    width: 100%;
    border-left: none;
    text-align: left;
    justify-content: flex-start;
    flex-wrap: wrap;
    border-bottom: 1px solid #ddd;
  }
}

#main-header .header-right nav .navbar-nav .nav-item:before {
  display: block;
  content: '';
  background-color: transparent;
  color: transparent;
  width: 0;
  height: 0;
  opacity: 0;
}

#main-header .header-right nav .navbar-nav .nav-item.active, #main-header .header-right nav .navbar-nav .nav-item:hover {
  background: #f6f6f6;
  cursor: pointer;
  -webkit-transition: all .3s ease;
  -moz-transition: all .3s ease;
  -ms-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
  -webkit-backface-visibility: hidden;
  -webkit-perspective: 1000px;
  -moz-perspective: 1000px;
  -ms-perspective: 1000px;
  perspective: 1000px;
}

#main-header .header-right nav .navbar-nav .nav-item.active .nav-link, #main-header .header-right nav .navbar-nav .nav-item:hover .nav-link {
  color: #018ec4;
}

#main-header .header-right nav .navbar-nav .nav-item .nav-link {
  color: #333;
  text-align: center;
  font-weight: bold;
  font-size: 14px;
  font-size: 1.4rem;
}

@media screen and (max-width: 768px) {
  #main-header .header-right nav .navbar-nav .nav-item .nav-link {
    display: block;
    width: 100%;
    text-align: left;
  }
}

#main-header .header-right nav .navbar-nav .nav-item.dropdown {
  position: relative;
}

#main-header .header-right nav .navbar-nav .nav-item.dropdown .dropdown-menu {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #fff;
  -webkit-transition: all .3s ease;
  -moz-transition: all .3s ease;
  -ms-transition: all .3s ease;
  -o-transition: all .3s ease;
  transition: all .3s ease;
  -webkit-backface-visibility: hidden;
  -webkit-perspective: 1000px;
  -moz-perspective: 1000px;
  -ms-perspective: 1000px;
  perspective: 1000px;
}

@media screen and (min-width: 769px) {
  #main-header .header-right nav .navbar-nav .nav-item.dropdown .dropdown-menu {
    display: block !important;
  }
}

@media screen and (max-width: 768px) {
  #main-header .header-right nav .navbar-nav .nav-item.dropdown .dropdown-menu {
    position: relative;
    opacity: 1;
    visibility: visible;
    display: none;
    background-color: transparent;
    margin-top: 10px;
  }
}

