.point-introduction {
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
  h3 {
    font-size: 27px;
    line-height: 1.25;
    position: relative;
    margin: 0 0 45px;
    text-align: center;
    padding: 25px 0;
    color: #000;
    border-top: 1px solid #000;
    border-bottom: 1px solid #000;
    @media screen and (max-width: 768px) {
      margin: 0 0 30px;
      @include font-px-to-rem(21);
    }
  }
  .block-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .left {
      width: 75%;
      padding-right: 30px;
      padding-top: 60px;
      @media screen and (max-width: 768px) {
        width: 100%;
        padding-top: 0;
        padding-right: 0;
        padding-bottom: 30px;
      }
    }
    .right {
      width: 25%;
      @media screen and (max-width: 768px) {
        width: 100%;
        text-align: center;
      }
    }
  }
}

.point-introduction-2 {
  padding-bottom: 75px;
  @media screen and (max-width: 768px) {
    padding-bottom: 45px;
  }
  .container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .text {
      width: 75%;
      padding-top: 30px;
      padding-right: 30px;
      @media screen and (max-width: 768px) {
        padding-top: 0;
        padding-right: 0;
        width: 100%;
      }
    }
    .img {
      width: 25%;
      @media screen and (max-width: 768px) {
        padding-top: 30px;
        width: 100%;
        text-align: center;
      }
    }
  }
}

