.main-visual {
  display: block;
  background: url('../images/photos/index-mainvisual-bg.jpg') center center /cover no-repeat;
  padding: 50px;
  @media screen and (max-width: 768px) {
    padding: 25px;
  }
  .main-visual-action {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    .icon {
      color: #fff;
      text-align: center;
      @include font-px-to-rem(18);
      background: rgba(0, 0, 0, .75);
      padding: 5px;
      width: 24%;
      margin: 0 .5%;
      cursor: pointer;
      @media screen and (max-width: 768px) {
        width: 100%;
        margin-bottom: 10px;
      }
    }
  }
}

.modal {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  outline: 0;
  overflow-x: hidden;
  overflow-y: auto;
  background-color: rgba(0, 0, 0, .5);
  .modal-dialog {
    max-width: 500px;
    margin: 20px auto;
    position: relative;
    width: auto;
    pointer-events: none;
    .modal-content {
      position: relative;
      display: flex;
      flex-direction: column;
      width: 100%;
      pointer-events: auto;
      background-color: #fff;
      border: 1px solid rgba(0, 0, 0, .2);
      border-radius: .3rem;
      outline: 0;
      .modal-header {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        padding: 15px;
        border-bottom: 1px solid #e9ecef;
        border-top-left-radius: .3rem;
        border-top-right-radius: .3rem;
        font-weight: bold;
        @include font-px-to-rem(16);
      }
      .modal-body {
        position: relative;
        flex: 1 1 auto;
        padding: 10px;
        ul {
          list-style: none;
          margin-left: 40px;
          li {
            padding-left: 48px;
            line-height: 48px;
            position: relative;
            @include font-px-to-rem(18);
            &:before {
              display: block;
              content: "";
              background: url('../images/icons/checkmark.png') left center /30px no-repeat;
              width: 30px;
              height: 48px;
              position: absolute;
              top: 0;
              left: 0;
              right: 0;
            }
          }
        }
      }

      .modal-footer {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 1rem;
        border-top: 1px solid #e9ecef;
        .toggle-modal-off {
          background: #038fc4;
          color: #fff;
          border: 1px solid #038fc4;
          position: relative;
          font-weight: bold;
          font-size: 16px;
          line-height: 20px;
          padding: 10px 15px;
          &:hover {
            cursor: pointer;
          }
        }
      }
    }
  }
}

.intro-section-01 {
  display: flex;
  flex-wrap: wrap;
  .block {
    text-align: center;
    padding: 20px;
    width: 50%;
    @media screen and (max-width: 768px) {
      width: 100%;
    }
    &.left {
      background: #018ec4;
    }
    &.right {
      background-color: #eb8845;
    }
    p {
      color: white;
      line-height: 1.8;
      @include font-px-to-rem(20);
      &:last-child {
        font-family: 'Amatic SC', cursive;
        font-weight: bold;
        line-height: 1.4;
        @include font-px-to-rem(24);
      }
    }
  }
}

.intro-section-02 {
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
}

.intro-section-03 {
  background: url("../images/photos/bg-index-3.jpg") center center /cover no-repeat #00c1ff;
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
  h2 {
    color: #fff;
    text-align: left;
    font-weight: bold;
    margin-bottom: 30px;
    @include font-px-to-rem(60);
    @media screen and (max-width: 768px) {
      @include font-px-to-rem(29);
    }
  }
  p {
    color: white;
    line-height: 2.4;
    padding-left: 48px;
    font-weight: 500;
    @include font-px-to-rem(18);
    @media screen and (max-width: 768px) {
      padding-left: 24px;
      @include font-px-to-rem(16);
    }
  }
  .cta {
    text-align: center;
    margin-top: 20px;
  }
}

.intro-section-04 {
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
  .point-list {
    display: flex;
    flex-wrap: wrap;
    .point-item {
      @media screen and (min-width: 769px) {
        width: 32%;
        margin-right: 2%;
        margin-bottom: 2%;
        &:nth-child(3n) {
          margin-right: 0;
        }
      }
      @media screen and (max-width: 768px) and (min-width: 481px) {
        width: 48%;
        margin-right: 4%;
        margin-bottom: 4%;
        &:nth-child(2n) {
          margin-right: 0;
        }
      }
      @media screen and (max-width: 480px) {
        width: 100%;
        margin-bottom: 30px;
      }
      h3 {
        color: #038fc4;
        text-align: center;
        margin: 10px 0;
        @include font-px-to-rem(24);
      }
      .cta {
        text-align: center;
        margin-top: 30px;
        .button {
          @include font-px-to-rem(16);
        }
      }
    }
  }
}

.intro-section-05 {
  padding: 30px 0;
  background: #000;
  .block-container {
    display: flex;
    flex-wrap: wrap;
    .block {
      width: 50%;
      @media screen and (max-width: 768px) {
        width: 100%;
      }
      &.left {
        padding-right: 30px;
        @media screen and (max-width: 768px) {
          text-align: center;
          padding-right: 0;
        }
        h2 {
          margin-bottom: 15px;
        }
        p {
          margin-bottom: 15px;
          color: white;
          small {
            @include font-px-to-rem(14);
          }
          &.comment {
            padding-left: 48px;
            line-height: 2;
            @include font-px-to-rem(18);
          }
        }
      }
    }
  }
}

