.support-introduction {
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
  h3 {
    line-height: 1.25;
    position: relative;
    margin: 0 0 45px;
    text-align: center;
    padding: 25px 0;
    color: #000;
    border-top: 1px solid #000;
    border-bottom: 1px solid #000;
    @include font-px-to-rem(27);
    @media screen and (max-width: 768px) {
      font-size: 21px;
      margin: 0 0 30px;
    }
  }
  .manga-wrapper {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    li {
      @media screen and (min-width: 760px) {
        width: 31%;
        margin: 1%;
      }
      @media screen and (min-width: 481px) and (max-width: 768px) {
        width: 48%;
        margin-right: 4%;
        margin-bottom: 4%;
        &:nth-child(2n) {
          margin-right: 0;
        }
      }
      @media screen and (max-width: 480px) {
        width: 100%;
        margin-bottom: 4%;
      }
    }
  }
}

.support-local {
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
}

.support-permanent {
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
  .container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .img {
      width: 40%;
      padding-top: 60px;
      @media screen and (max-width: 768px) {
        width: 100%;
        padding-top: 0;
        padding-bottom: 30px;
      }
    }
    .text {
      width: 60%;
      padding-left: 30px;
      @media screen and (max-width: 768px) {
        width: 100%;
        padding-left: 0;
      }
    }
    .notice-box {
      margin-top: 30px;
      width: 100%;
      background: #fff;
      display: block;
      position: relative;
      border: 4px solid #ff7c80;
      h5 {
        background-color: rgba(255, 124, 128, .2);
        text-align: center;
        font-weight: bold;
        padding: 20px 30px;
        @include font-px-to-rem(21);
        @media screen and (max-width: 768px) {
          padding: 15px;
          @include font-px-to-rem(16);
        }
      }
      ul {
        padding: 30px 30px 30px 60px;
        @media screen and (max-width: 768px) {
          padding: 15px 30px;
        }
        li {
          margin-bottom: 10px;
          list-style: disc;
          @include font-px-to-rem(16);
          @media screen and (max-width: 768px) {
            margin-bottom: 5px;
            @include font-px-to-rem(14);
          }
          &:last-child {
            margin-bottom: 0;
          }
        }
      }
    }
  }
}

.support-visa {
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
  .container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .img {
      width: 30%;
      @media screen and (max-width: 768px) {
        width: 100%;
      }
    }
    .text {
      width: 70%;
      padding-right: 30px;
      @media screen and (max-width: 768px) {
        width: 100%;
        padding-right: 0;
        margin-bottom: 30px;
      }
    }
  }
}

.support-service {
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
  .alert-list {
    display: flex;
    flex-wrap: wrap;
    .item {
      color: white;
      padding: 45px;
      border-radius: 5px;
      @media screen and (min-width: 769px) {
        width: 32%;
        margin-right: 2%;
        margin-bottom: 2%;
        &:nth-child(3n) {
          margin-right: 0;
        }
      }
      @media screen and (max-width: 768px) {
        width: 100%;
        margin-bottom: 30px;
        padding: 30px;
      }
      &:nth-child(1) {
        background-color: #ffc000;
      }
      &:nth-child(2) {
        background-color: #3cc;
      }
      &:nth-child(3) {
        background-color: #92d050;
      }
      h4 {
        text-align: center;
        font-weight: bold;
        @include font-px-to-rem(24);
        @media screen and (max-width: 768px) {
          @include font-px-to-rem(18);
        }
      }
      svg {
        height: 60px;
        margin: 30px auto;
        display: block;
      }
      p {
        line-height: 1.6;
        font-weight: 500;
        @include font-px-to-rem(16);
        @media screen and (max-width: 768px) {
          @include font-px-to-rem(14);
        }
      }
    }
  }
}

.support-dl {
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
  .leaflet-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    .item {
      width: 32%;
      margin: 0 2%;
      text-align: center;
      @media screen and (max-width: 768px) {
        width: 100%;
        margin: 0 0 30px;
      }
      img {
        width: 100%;
        margin-bottom: 30px;
      }
    }
  }
}