/* ====================================================
Heading
==================================================== */
.section-heading {
  text-align: center;
  margin-bottom: 75px;
  font-weight: normal;
  @include font-px-to-rem(42);
  @media screen and (max-width: 768px) {
    margin-bottom: 30px;
    @include font-px-to-rem(24);
  }
}

.section-banner {
  padding: 90px 30px;
  position: relative;
  &:after {
    display: block;
    content: '';
    background-color: rgba(0, 0, 0, .25);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .banner-content {
    padding: 60px;
    background-color: rgba(0, 0, 0, .5);
    max-width: 1500px;
    margin: 0 auto;
    color: #fff;
    position: relative;
    z-index: 1;
    @media screen and (max-width: 768px) {
      padding: 30px;
    }
    h2, h3 {
      line-height: 1.2;
      text-align: center;
      color: #fff;
      font-weight: bold;
      border-bottom: 1px solid #fff;
      padding-bottom: 45px;
      margin-bottom: 45px;
      @include font-px-to-rem(36);
      @media screen and (max-width: 768px) {
        padding-bottom: 20px;
        margin-bottom: 20px;
        @include font-px-to-rem(24);
      }
    }
    .lead {
      text-align: center;
      font-weight: bold;
      @include font-px-to-rem(30);
    }
    .sub-lead {
      text-align: center;
      margin-top: 15px;
      @include font-px-to-rem(18);
    }
  }
  &.page-support {
    background: url("../images/photos/support-banner.jpg") no-repeat center center;
    background-size: cover;
  }
  &.page-recruit {
    background: url("../images/photos/recruit-banner.jpg") no-repeat center center;
    background-size: cover;
  }
  &.page-point {
    background: url("../images/photos/point-banner.jpg") no-repeat center center;
    background-size: cover;
  }
  &.page-interview {
    background: url("../images/photos/interview-banner.jpg") no-repeat center center;
    background-size: cover;
  }
  &.page-contact {
    background: url("../images/photos/contact-banner.jpg") no-repeat center center;
    background-size: cover;
  }
  &.page-company {
    background: url("../images/photos/company-banner.jpg") no-repeat center center;
    background-size: cover;
    .lead {
      @include font-px-to-rem(18);
      span {
        @include font-px-to-rem(24);
      }
    }
  }
}

.section-ttl {
  line-height: 1.25;
  position: relative;
  margin: 0 0 60px;
  text-align: center;
  padding: 30px 15px;
  color: #fff;
  @include font-px-to-rem(27);
  @media screen and (max-width: 768px) {
    font-size: 21px;
    margin: 0 0 60px !important;
  }
  &:after {
    content: "";
    border-width: 25px;
    border-style: solid;
    position: absolute;
    top: 100%;
    @include h-align;
  }
  &.pink {
    background-color: #ff7c80;
    &:after {
      border-top-color: #ff7c80;
    }
  }
}

.section-sub-ttl {
  line-height: 1.25;
  position: relative;
  margin-bottom: 45px;
  color: #333;
  text-align: center;
  font-weight: bold;
  padding-bottom: 30px;
  @include font-px-to-rem(24);
  @media screen and (max-width: 768px) {
    @include font-px-to-rem(18);
  }
  &:after {
    content: "";
    width: 3.2em;
    height: 2px;
    position: absolute;
    left: 50%;
    right: 50%;
    bottom: 0;
    margin: 0 -1.6em;
    background: #000;
    display: block;
  }
  span {
    line-height: 1.25;
    position: relative;
    margin-bottom: 45px;
    color: #333;
    text-align: center;
    padding-bottom: 30px;
    @include font-px-to-rem(16);
    @media screen and (max-width: 768px) {
      @include font-px-to-rem(14);
    }
  }
}

.heading-intro {
  line-height: 1.25;
  position: relative;
  margin-bottom: 45px;
  text-align: center;
  padding: 25px 0;
  color: #000;
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
  @include font-px-to-rem(27);
  @media screen and (max-width: 768px) {
    margin-bottom: 30px;
    @include font-px-to-rem(21);
  }
}

/* ====================================================
Button
==================================================== */
.button {
  display: inline-block;
  color: white;
  background-color: #038fc4;
  line-height: 1.6;
  padding: 5px 20px;
  position: relative;
  border-radius: 2px;
  font-weight: 500;
  @include font-px-to-rem(14);
  @include smooth-transition;
  &:hover {
    opacity: .7;
    cursor: pointer;
    @include smooth-transition;
  }
  &.rounded {
    border-radius: 30px;
  }
  &.block {
    width: 100%;
    font-weight: bold;
    @include font-px-to-rem(16);
  }
  &.has-left-arrow {
    &:before {
      content: "";
      width: 6px;
      height: 6px;
      background: #fff;
      left: 0;
      display: block;
      position: absolute;
      @include v-align;
    }
    &:after {
      content: "";
      position: absolute;
      border-width: 6px;
      border-style: solid;
      border-color: transparent transparent transparent #fff;
      @include v-align;
      left: 5px;
      display: block;
    }
  }
}

/* ====================================================
List
==================================================== */
.check {
  list-style: none;
  li {
    line-height: 48px;
    padding-left: 48px;
    position: relative;
    @include font-px-to-rem(18);
    @media screen and (max-width: 768px) {
      @include font-px-to-rem(16);
    }
    &:before {
      content: "";
      background: url("../images/icons/checkmark-white.png") left center /30px no-repeat;
      width: 30px;
      height: 48px;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
    }
  }
}

/* ====================================================
Breadcrumbs
==================================================== */
.breadcrumb-wrapper {
  .breadcrumb {
    background: transparent;
    font-size: 12px;
    padding: 15px 0;
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 15px;
    .breadcrumb-item {
      &:before {
        display: inline-block;
        padding-right: .5rem;
        color: #6c757d;
        content: "/";
      }
      &:first-child {
        &:before {
          display: none;
        }
      }
      a {
        color: #008ec3;
        font-weight: 500;
      }
    }
  }
}

/* ====================================================
Others
==================================================== */
.video-wrapper {
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-width: 980px;
  .iframe-wrapper {
    position: relative;
    padding-bottom: 56.25%; /* 16:9 */
    height: 0;
    iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
}

.flow-box {
  .item {
    display: flex;
    flex-wrap: wrap;
    .text, .img {
      position: relative;
      width: 50%;
      @media screen and (max-width: 768px) {
        width: 100%;
      }
    }
    &:nth-child(1) {
      .text {
        background: #92d050;
        &:after {
          border-left-color: #92d050;
        }
      }
    }
    &:nth-child(2) {
      .text {
        background: #93cddd;
        &:after {
          border-left-color: #93cddd;
        }
      }
    }
    &:nth-child(3) {
      .text {
        background: #ffc000;
        &:after {
          border-left-color: #ffc000;
        }
      }
    }
    &:nth-child(4) {
      .text {
        background: #3cc;
        &:after {
          border-left-color: #3cc;
        }
      }
    }
    &:nth-child(5) {
      .text {
        background: #C6C;
        &:after {
          border-left-color: #C6C;
        }
      }
    }
    .text {
      color: white;
      display: flex;
      flex-wrap: wrap;
      align-content: center;
      padding: 30px;
      h3 {
        width: 100%;
        font-weight: bold;
        padding-left: 60px;
        @include font-px-to-rem(21);
      }
      p {
        width: 100%;
        padding-left: 60px;
      }
      span {
        font-weight: bold;
        @include font-px-to-rem(72);
        position: absolute;
        @include v-align;
        left: 30px;
        line-height: 1;
      }
      &:after {
        display: block;
        content: '';
        width: 0;
        height: 0;
        border: 25px solid transparent;
        position: absolute;
        left: 100%;
        top: 50%;
        transform: translate(0, -50%);
        z-index: 1;
        @media screen and (max-width: 768px) {
          top: 100%;
          left: 50%;
          transform: translate(-50%, 0) rotate(90deg);
        }
      }
    }
    .img {
      padding-bottom: 300px;
      @media screen and (max-width: 768px) {
        padding-bottom: 200px;
      }
      img {
        position: absolute;
        object-fit: cover;
        width: 100%;
        height: 100%;
        @include v-h-align;
      }
    }
  }
}