.contact-form {
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
  table {
    width: 100%;
    margin-bottom: 30px;
    th, td {
      padding: 29px 19px;
      background-color: #fff;
      border: 1px solid #dddddd;
      @include font-px-to-rem(16);
      @media screen and (max-width: 768px) {
        display: block;
        width: 100%;
        padding: 15px;
      }
    }
    th {
      font-weight: bold;
      width: 240px;
      background-color: #efefef;
      @media screen and (max-width: 768px) {
        width: 100%;
      }
      &.required {
        background-image: url("data:image/svg+xml;base64,");
        background-position: right center, left top;
        background-size: 46px;
        background-repeat: no-repeat, repeat;
      }
    }
    td {
      input[type='text'] {
        padding: 14px;
        width: 100%;
        @include font-px-to-rem(14);
      }
      .form {
        &-s {
          width: 100px !important;
        }
        &-m {
          width: 240px !important;
        }
      }
      p {
        margin-bottom: 10px;
      }
      textarea {
        resize: vertical;
        width: 100%;
        border: 1px solid #dddddd;
        padding: 14px;
      }
      .form-inline {
        display: flex;
        flex-wrap: wrap;
        label {
          display: flex;
          align-items: center;
          margin-right: 10px;
        }
      }
    }
  }
  .box {
    background: #fff;
    display: block;
    width: 100%;
    position: relative;
    border: 4px solid #93cddd;
    .header {
      background: rgba(147,205,221,.2);
      @include font-px-to-rem(21);
      font-weight: bold;
      color: #000;
      text-align: center;
      padding: 15px;
    }
    .content {
      height: 200px;
      overflow-y: scroll;
      padding: 28px;
      h5 {
        padding-bottom: 9px;
        border-bottom: 1px dotted #666;
        font-weight: bold;
        margin: 30px 0 15px;
        @include font-px-to-rem(16);
      }
      ol {
        li {
          list-style: none;
          margin-bottom: 5px;
          @include font-px-to-rem(16);
        }
      }
    }
  }
}