.recruit-introduction {
  background: url("../images/photos/page2.jpg") no-repeat center center;
  background-size: cover;
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
  h3 {
    color: #fff;
    text-align: left;
    font-weight: bold;
    margin-bottom: 45px;
    @include font-px-to-rem(36);
    @media screen and (max-width: 768px) {
      margin-bottom: 30px;
      @include font-px-to-rem(24);
    }
  }
  ul {
    padding-left: 30px;
    margin-bottom: 30px;
    li {
      color: white;
      margin-bottom: 10px;
      list-style: disc;
      @include font-px-to-rem(16);
    }
  }
  .cta {
    .button {
      margin-right: 30px;
      @include font-px-to-rem(16);
    }
    @media screen and (max-width: 768px) {
      text-align: center;
      .button {
        margin-bottom: 15px;
        width: 100%;
      }
    }
  }
}

.recruit-introduction-2 {
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
  h3 {
    font-size: 27px;
    line-height: 1.25;
    position: relative;
    margin: 0 0 45px;
    text-align: center;
    padding: 25px 0;
    color: #000;
    border-top: 1px solid #000;
    border-bottom: 1px solid #000;
  }
}

.recruit-introduction-3 {
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
  .member-list {
    display: flex;
    flex-wrap: wrap;
    .item {
      @media screen and (min-width: 769px) {
        width: 32%;
        margin-right: 2%;
        margin-bottom: 2%;
        &:nth-child(3n) {
          margin-right: 0;
        }
      }
      @media screen and (max-width: 768px) and (min-width: 481px) {
        width: 48%;
        margin-right: 4%;
        margin-bottom: 4%;
        &:nth-child(2n) {
          margin-right: 0;
        }
      }
      @media screen and (max-width: 480px) {
        width: 100%;
        margin-bottom: 30px;
      }
      h3 {
        color: #038fc4;
        text-align: center;
        margin: 10px 0;
        @include font-px-to-rem(24);
      }
    }
  }
}