.page-company {
  .alert-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .item {
      width: 31%;
      padding: 45px;
      border-radius: 5px;
      @media screen and (max-width: 768px) {
        width: 100%;
        margin-bottom: 20px;
        padding: 30px;
      }
      h4 {
        font-weight: bold;
        text-align: center;
        margin-bottom: 30px;
        @include font-px-to-rem(24);
        @media screen and (max-width: 768px) {
          margin-bottom: 15px;
          @include font-px-to-rem(16);
        }
      }
      &:first-child {
        background-color: #ffc000;
      }
      &:nth-child(2) {
        background-color: #3cc;
      }
      &:last-child {
        background-color: #92d050;
      }
    }
  }
}

.company-intro-01 {
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
  table {
    width: 100%;
    margin-bottom: 30px;
    th, td {
      padding: 29px 19px;
      background-color: #fff;
      border: 1px solid #dddddd;
      @include font-px-to-rem(16);
      @media screen and (max-width: 768px) {
        display: block;
        width: 100%;
        padding: 15px;
      }
    }
    th {
      font-weight: bold;
      background-color: #efefef;
    }
  }
}

.company-intro-02 {
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
  a {
    @include smooth-transition;
    &:hover {
      opacity: .7;
      @include smooth-transition;
    }
  }
}

.company-intro-03 {
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
  .company-banner-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .item {
      width: 48%;
      margin-bottom: 4%;
      @include smooth-transition;
      @media screen and (max-width: 768px) {
        width: 100%;
        margin-bottom: 30px;
      }
      &:hover {
        opacity: .7;
        @include smooth-transition;
      }
    }
  }
}