.section-interview-01 {
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
}

.section-interview-02 {
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
}

.section-interview-03 {
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
  .img-wrapper {
    img {
      width: 24%;
      @media screen and (max-width: 768px) {
        width: 100%;
      }
    }
  }
}

.section-interview-04 {
  padding: 75px 0;
  @media screen and (max-width: 768px) {
    padding: 45px 0;
  }
  .img-wrapper {
    img {
      width: 49%;
      @media screen and (max-width: 768px) {
        width: 100%;
      }
    }
  }
}

.img-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  @media screen and (max-width: 768px) {
    flex-wrap: wrap;
    padding: 0 15px;
    img {
      margin-bottom: 10px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.interview-local {
  padding-bottom: 75px;
  @media screen and (max-width: 768px) {
    padding-bottom: 45px;
  }
  .container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .text {
      width: 70%;
      padding-right: 30px;
      @media screen and (max-width: 768px) {
        width: 100%;
        margin-bottom: 30px;
        padding-right: 0;
      }
    }
    .img {
      width: 30%;
      img {
        border-radius: 50%;
      }
      @media screen and (max-width: 768px) {
        width: 100%;
        text-align: center;
      }
    }
  }
}